body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* D3 Styling */

.title__container {
  background-color: #F0F0F0;
  padding: 1rem;
}

.heading-title {
  color: #000;
  font-size: 2.4rem;
  text-align: center;
}

.box__heading {
  font-size: 1.5rem;
}

.node rect {
  cursor: move;
  fill-opacity: .9;
  shape-rendering: crispEdges;
}
.node text {
  pointer-events: none;
  text-shadow: 0 1px 0 #fff;
}
.link {
  fill: none;
  stroke: #000;
  stroke-opacity: .15;
}
.link:hover {
  stroke-opacity: .25;
}
svg, canvas {
  position: absolute;
}

#us-pipeline-visualisation-container {
  position: relative;
  background-color: #F0F0F0;
}

.canvas-hidden {
  visibility: hidden;
}
.Visualisation {
  min-height: 60vh;
}

.App-header {
  min-height: 40vh;
  padding-top: 1.5rem;
}

.Visualisation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

.box__heading {
  font-weight: bold;
}


.terminal-color {
  width: 16px;
  height: 16px;
  line-height: 16px;
  display: inline-block;
  margin-right: 5px;
  vertical-align: -2px;
  border:1px solid #F1F1F1;
}

.tabs-ops {
  position: relative;
}

.shipper-selector{
  position: absolute;
  right: 0;
  top: 0;
}
.shipper-selector > span {
  vertical-align: -5px;
  display: inline-block;
  margin-right: 5px;
  font-weight: bold;
}
